.App {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: Inconsolata, sans-serif;
}

.dark-switch {
  cursor: pointer;
}

.dark {
  background: #333;
  color: white;
}

.time {
  font-size: 48px;
}

.input-time {
  margin-top: 30px;
}

.input-time input {
  padding: 10px;
  width: 60px;
  font-size: 20px;
  outline: none;
  border: none;
  border-bottom: 1px solid black;
  margin: 10px;
  text-align: center;
}

.dark .input-time input {
  background: #333;
  color: white;
  border-color: white;
}

.remaining {
  text-align: center;
  font-style: italic;
  font-size: 12px;
}

.hidden {
  display: none;
}

.input-alarm input {
  cursor: pointer;
  font-size: 20px;
  display: block;
  margin: auto;
  padding: 20px;
  padding-left:50px;
  padding-right:50px;
  color: #fff;
  background-color: #d9534f;
  border-color: #d43f3a;
}

footer {
  bottom: 1%;
  position: absolute;
}

footer p {
  margin-bottom: 10px;
}

footer q {
  font-size: 15px;
}

a {
    color: white;
}

input:invalid {
  color: #de4433 !important;
}



